@import url('https://fonts.googleapis.com/css?family=Unica+One&display=swap');
@import '/style/vars.scss';
@import '/style/welcome_screen.scss';


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    font-size: 62.5%;
    color: $color-black;
    background-color: $color-white;
    overflow: hidden;
}

.main {
    width: 50%;
    height: 50%;
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    margin: auto;
    border-collapse: collapse;
    tr:first-of-type .cell{
        border-top: none;
    }
    tr:last-of-type .cell{
        border-bottom: none;
    }
    tr .cell:first-of-type{
        border-left: none;
    }
    tr .cell:last-of-type{
        border-right: none;
    }
    .cell {
        border: 3px solid $color-white;
        width: 5rem; height: 5rem;
    }
}

